import { Component, OnInit, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';

import { Person } from '../person';
import { PersonsService } from '../persons.service';

import { ToolsService } from '../../shared/tools.service';
import { MaterializeAction } from 'angular2-materialize/src';

@Component({
  selector: 'app-persons',
  templateUrl: './persons.component.html',
  styleUrls: ['./persons.component.css']
})
export class PersonsComponent implements OnInit {
  public persons: Person[];
  public currentPerson: Person;
  public modalActions = new EventEmitter<string|MaterializeAction>();

  constructor(
    private _personsService: PersonsService,
    private _tools: ToolsService,
    private _router: Router
  ) { }

  ngOnInit() {
    this._tools.getPagination().clear();
    
    this._tools.getToolbar().change({
      title: "Список людей",
      name: "persons",
      actions: {
        add: () => this._router.navigate(['persons/add'])
      }
    });
    this._personsService.get()
      .subscribe((response: any) => {
        this.persons = response.items;
      });
  }

  openModal(item) {
    this.currentPerson = item;
    this.modalActions.emit({action:"modal",params:['open']});
  }

  closeModal() {
    this.modalActions.emit({action:"modal",params:['close']});
  }

  deleteItem(item) {
    let self = this;

    this._personsService.remove(item._id)
      .subscribe((data: any) => {
        if(data.status == 'ok') {
          this.persons.find((person, index) => {
            if(item._id == person._id) {
              self.currentPerson = null;
              self.persons.splice(index, 1);
              return true;
            }
          });
          this._tools.getToast().toast("Информация удалена");
        }
      });
  }

}



