import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router, Params } from '@angular/router';
import { FormControl, FormBuilder, FormGroup, Validators } from '@angular/forms';

import { Person } from '../person';
import { PersonsService } from '../persons.service';
import { ToolbarService } from '../../shared/toolbar.service';
import { ToolsService } from '../../shared/tools.service';

@Component({
  selector: 'app-person-update',
  templateUrl: './person-update.component.html',
  styleUrls: ['./person-update.component.css']
})
export class PersonUpdateComponent implements OnInit {
  public updateForm: FormGroup;
  public submitted: boolean = false;
  public person: Person;
  public datePickerOptions: any;

  constructor(
    private _route: ActivatedRoute,
    private _personsService: PersonsService,
    private _tools: ToolsService
  ) { }

  ngOnInit() {
    this._tools.getToolbar().change({
      title: "Обновить информацию",
      name: "persons.update"
    });

    this.updateForm = new FormGroup({
      _id: new FormControl('', [<any>Validators.required]),
      first_name: new FormControl('', [<any>Validators.required, <any>Validators.minLength(2)]),
      second_name: new FormControl(''),
      last_name: new FormControl('', [<any>Validators.required, <any>Validators.minLength(2)]),
			gender: new FormControl('', [<any>Validators.required]),
			birthdate: new FormControl(''),
			phone: new FormControl('', [<any>Validators.required]),
			email: new FormControl('')
    });

    this._route.params.forEach((params: Params) => {
			let id = params['id']; 
			this._personsService.getOne(id)
				.subscribe(person => {
					this.person = new Person(person);
					(<FormGroup>this.updateForm)
            			.patchValue(this.person);
				});
		});
  }

  clearForm(options?: any[]) : void {
    if(this.person != undefined) {
      this.person.clear();
    } else {
      this.person = new Person();
    }
  }

  onSubmit(values, valid) {
    //console.log(values);
    if(valid) {
      this._personsService.update(values)
        .subscribe((data) => this._tools.getToast().toast("Информация успешно обновлена!"));
    }
  }

}



