import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FormGroup } from '@angular/forms';

import { Person } from '../../person';

@Component({
  selector: 'app-person-form',
  templateUrl: './person-form.component.html',
  styleUrls: ['./person-form.component.css']
})
export class PersonFormComponent implements OnInit {
  @Output('submitForm') 
    eSubmit: EventEmitter<Person> = new EventEmitter<Person>();
  @Output('close')
    eClose: EventEmitter<void> = new EventEmitter<void>();
  @Output('selectImage') 
    eSelectImage: EventEmitter<void> = new EventEmitter<void>();
  
  @Input('form') form: FormGroup;
  @Input('person') person: Person;

  constructor() { }

  ngOnInit() { }

  onSelectImage() {
    this.eSelectImage.emit();
  }

  onSubmit($event) {
    this.eSubmit.emit(this.form.value);
  }

  onClose() {
    this.eClose.emit();
  }

  disable() {
    this.form.disable();
  }

}



