import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { MaterializeModule } from "angular2-materialize/src";
import { SharedModule } from '../shared/shared.module';

import { PersonsService } from './persons.service';

import { PersonsWidgetModule } from './_widgets/persons.widget';

import { PersonAddComponent } from './person-add/person-add.component';
import { PersonsComponent } from './persons/persons.component';
import { PersonUpdateComponent } from './person-update/person-update.component';

import { AuthGuard } from '../+users/_guards/auth-guard.service';
import { HttpClient } from '../+users/_services/http-client.service';

const PERSONS_ROUTES: Routes = [
  { path: '', component: PersonsComponent, canActivate: [AuthGuard] },
  { path: 'add', component: PersonAddComponent },
  { path: 'update/:id', component: PersonUpdateComponent }
];

@NgModule({
  imports: [
    CommonModule,
    RouterModule.forChild(PERSONS_ROUTES),
    ReactiveFormsModule, FormsModule,
    MaterializeModule,
    SharedModule,
    PersonsWidgetModule
  ],
  declarations: [PersonAddComponent, PersonsComponent, PersonUpdateComponent],
  providers: [PersonsService, AuthGuard, HttpClient],
  exports: [PersonsComponent]
})
export class PersonsModule { }


