import { Component, OnInit, EventEmitter, Input, Output } from '@angular/core';
import { Image } from '../../file';

const UPLOAD_PATH = '/api/v1/uploads/';

@Component({
  selector: 'app-images-list',
  templateUrl: './images-list.component.html',
  styleUrls: ['./images-list.component.css']
})
export class ImagesListComponent implements OnInit {
  @Input('images') images: Image[] = [];
  @Input('selectedImages') selectedImages: Image[] = [];

  @Output('checkItem')
    eCheck: EventEmitter<Image> = new EventEmitter<Image>();
  @Output('deleteItem')
    eDelete: EventEmitter<Image> = new EventEmitter<Image>();

  images_path = UPLOAD_PATH; 

  constructor() { }

  ngOnInit() {}

  onCheck(item: Image): void {
    this.eCheck.emit(item);
  }

  onDelete(item: Image): void {
    this.eDelete.emit(item);
  }

  isSelect(item: Image): boolean {
    return !!this.selectedImages.find((current) => {
      if(current._id === item._id) return true;
    });
  }

}



