import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { MaterializeModule } from "angular2-materialize/src";
import { SharedModule } from '../../shared/shared.module';
import { UploadsWidgetModule } from '../../+uploads/_widgets/uploads.widget';

import { PersonsService } from '../persons.service';

import { PersonFormComponent } from './person-form/person-form.component';
import { PersonFormContentComponent } from './person-form-content/person-form-content.component';

@NgModule({
  imports: [
    CommonModule,
    ReactiveFormsModule, FormsModule,
    MaterializeModule,
    SharedModule,
    UploadsWidgetModule
  ],
  declarations: [PersonFormComponent, PersonFormContentComponent],
  providers: [PersonsService],
  exports: [PersonFormComponent, PersonFormContentComponent]
})
export class PersonsWidgetModule { }


