import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormBuilder, FormArray, Validators } from '@angular/forms';
import { Observable } from 'rxjs/Observable';

import { Person } from '../../person';

import { PersonsService } from '../../persons.service';
import { FilesService } from '../../../+uploads/files.service';

@Component({
  selector: 'app-person-form-content',
  templateUrl: './person-form-content.component.html',
  styleUrls: ['./person-form-content.component.css']
})
export class PersonFormContentComponent implements OnInit {  
  @Input('form') form: FormGroup = this.createFormGroup();
  @Input('person') 
    set person(item: Person) {
      console.log('И чё блять?', item);
      this._person = item || new Person();
      if(this._person.picture) {
        this.toogleSelectImage(item.picture);
      } 
      this._selectedImagesOld = this.selectedImages.slice(0);
    }

    get person(): Person {
      return this._person;
    }

  _person: Person = new Person();
  _basePicturePath: string = "/api/v1/uploads/thumbs/";
  images$: Observable<any[]>;
  selectedImages: any[] = [];
  foundPersons: Person[] = [];
  private _selectedImagesOld: any[] = [];

  constructor(
    private fb: FormBuilder,
    private _filesService: FilesService,
    private _personService: PersonsService
  ) {
    
  }

  reloadImages() {
    this.images$ = this._filesService.get();
  }

  private reloadPersons(search?: string) {
    let person$ = this._personService.get({text: search});

    person$.subscribe((persons) => {
      this.foundPersons = persons.items.map((item) => {
        return new Person(item);
      });
    });
  }

  ngOnInit() {
    this.patchValue(this.person);
  }

  onSearch(text) {
    this.reloadPersons(text);
  }

  patchValue(person: Person) {
    this.form.patchValue(person);
  }

  getValue() {
    return this.form.value;
  }

  disable() {
    this.form.disable();
  }

  toogleSelectImage(image) {
    this.selectedImages[0] = image;
  }

  onSuccessImages(result) {
    this.form.patchValue({picture: this.selectedImages[0]});
    this._selectedImagesOld = this.selectedImages.slice(0); // закрепляем результат
  }

  onCloseImages() {
    this.selectedImages = this._selectedImagesOld.slice(0); // возвращаем исходное значение
  }

  selectPerson(item: Person) {
    this.form.patchValue(item);
  }

  onCloseSearchPerson() {
    this.form.patchValue({_id: null});
  }

  createFormGroup() {
    return this.fb.group({
      _id: this.fb.control(''),
      first_name: this.fb.control('', Validators.required),
      second_name: this.fb.control(''),
      last_name: this.fb.control('', Validators.required),
      gender: this.fb.control(''),
      birthdate: this.fb.control(''),
      phone: this.fb.control('', Validators.required),
      email: this.fb.control(''),
      picture: this.fb.control('')
    });
  }

}



