import { Component, OnInit } from '@angular/core';
import { FormControl, FormBuilder, FormGroup, Validators } from '@angular/forms';

import { PersonsService } from '../persons.service';

import { ToolbarService } from '../../shared/toolbar.service';
import { ToastService } from '../../shared/toast.service';

import { Person } from '../person';

@Component({
  selector: 'app-person-add',
  templateUrl: './person-add.component.html',
  styleUrls: ['./person-add.component.css']
})
export class PersonAddComponent implements OnInit {
  public addForm: FormGroup;
  public submitted: boolean = false;
  public person: Person;
  public datePickerOptions: any;

  constructor(
    private _personsService: PersonsService,
    private _toastService: ToastService,
    private _toolbar: ToolbarService
    ) { }

  ngOnInit() {
    this._toolbar.change({
      title: "Добавить человека",
      name: "persons.add"
    });
    
    this.person = new Person();

    this.datePickerOptions = {
      selectMonths: true,
      selectYears: 50,
      monthsFull: ['Январь', 'Февраль', 'Март', 'Апрель', 'Май', 'Июнь', 'Июль', 'Август', 'Сентябрь', 'Октябрь', 'Ноябрь', 'Декабрь'],
      monthsShort: ['Янв', 'Фев', 'Мар', 'Апр', 'Май', 'Ин', 'Ил', 'Авг', 'Сент', 'Окт', 'Ноя', 'Дек'],
      weekdaysFull: ['Воскресенье', 'Понедельник', 'Вторник', 'Среда', 'Четверг', 'Пятница', 'Суббота'],
      weekdaysShort: ['Вск', 'Пн', 'Вт', 'Ср', 'Чт', 'Пт', 'Сб'],
      today: 'Сегодня',
      clear: 'x',
      close: 'Закрыть',
      format: 'dd.mm.yyyy'
    };
    
    this.addForm = new FormGroup({
      first_name: new FormControl('', [<any>Validators.required, <any>Validators.minLength(2)]),
      second_name: new FormControl(''),
      last_name: new FormControl('', [<any>Validators.required, <any>Validators.minLength(2)]),
			gender: new FormControl('', [<any>Validators.required]),
			birthdate: new FormControl(''),
			phone: new FormControl('', [<any>Validators.required]),
			email: new FormControl('')
    });

    this.addForm.patchValue(this.person);
  }

  onSubmit(values, valid) {
    this.person.setParams(values);
    
    if(valid) {
      this._personsService.add(this.person)
        .subscribe((data) => this._toastService.toast("Информация успешно добавлена!"));
    }
  }

}



